@import "../../../scss/helpers/placeholder";
@import "../../../scss/helpers/mixins";

$prefix: '.rise-blocks';

#{$prefix}-cta-wrapper {
    position: relative;
    padding: 100px 40px;
    background-repeat: no-repeat !important;
    background-size: cover !important;

    #{$prefix}-cta-content {
	    position: relative;
        z-index: 9;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        margin: 0 auto;
        max-width: 1140px;
	}
    
    @include media(sm) { 
        #{$prefix}-cta-content{
            max-width: 720px;
        }
    }

    @include media(xs){
        #{$prefix}-cta-content{
            max-width: 540px;
        }
    }

    #{$prefix}-cta-btn-wrapper {
        max-width: 30%;
        flex: 0 0 30%;
        text-align: right;

        @include media(xs) {
            max-width: 100%;
            flex: 0 0 100%;
            margin-top: 30px;
            text-align: left;
        }
        
        #{$prefix}-cta-btn {
            padding: 14px 30px;
            border-width: 0px;
            border-style: solid;
            font-weight: 400;
            line-height: 1.2;
            font-size: 16px;
            background-color: #000;
            color: rgb(255, 255, 255);  
            text-decoration: none;
            display: inline-block;
            &:hover {
                text-decoration: none;
                color: #fff;
                background: #f1262f;
            } 
        }
    }

    #{$prefix}-cta-title-wrapper {
        max-width: 70%;
        flex: 0 0 70%;
        text-align: left;
        
        @include media(xs) {
            max-width: 100%;
            flex: 0 0 100%;
            padding-right: 0;
        }

        > #{$prefix}-cta-title{
            position: relative;
        }
        
        #{$prefix}-cta-title {
            margin: 0;
            color: rgb(0, 0, 0);
            padding-bottom: 15px;
            font-size: 42px;
            font-weight: 700;
            line-height: 1.2;
        }

        #{$prefix}-cta-title-line {
            height: 3px;
            width: 50px;
            border-radius: 25%;
            background: #000;
            display: block;
        }
    }

    #{$prefix}-cta-overlay {
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        background-color: rgba(233, 233, 233, 0.5);
    }    
}







